import { FC } from "react";
import { FormGroup, Label, Input, Anchor, Button } from "@doar/components";
import { useForm } from "react-hook-form";
import { hasKey } from "@doar/shared/methods";
import {
    StyledTitle,
    StyledDesc,
    StyledLabelWrap,
    StyledDivider,
    StyledBottomText,
    StyledSignInWrap,
} from "./style";
import {Container, Row} from "../signup-form/style";

interface IFormValues {
    email: string;
    password: string;
}

const SigninForm: FC = () => {
    const {
        register,
        handleSubmit,
        formState: { errors },
    } = useForm();

    const onSubmit = (data: IFormValues) => {
        alert(JSON.stringify(data, null));
    };
    return (
        <StyledSignInWrap>
           <Container>
               <StyledTitle>Sign In</StyledTitle>
               <Row sm={100} md={100} lg={100} xl={100}>
                   <StyledDesc >Welcome back! Please signin to continue.</StyledDesc>
               </Row><br/><br/>
               <Row sm={100} md={100} lg={100} xl={100}>
                   <form  action="#" onSubmit={handleSubmit(onSubmit)} noValidate>
                       <Row sm={100} md={100} lg={100} xl={100}>

                           <FormGroup mb="20px" >
                               <Label display="block" mb="5px" htmlFor="email">
                                   Telephone Number
                               </Label>
                               <Input
                                   id="phone_number"
                                   type="text"
                                   placeholder="Enter your telephone number"
                                   feedbackText={errors?.phone_number?.message}
                                   state={hasKey(errors, "phone_number") ? "error" : "success"}
                                   showState={!!hasKey(errors, "phone_number")}
                                   {...register("phone_number", {
                                       required: "Telephone number is required",
                                       pattern: {
                                           value: /[+998]{3}[0-9]{2}[0-9]{8}/i,
                                           message: "invalid phone number",
                                       },
                                   })}
                               />
                           </FormGroup>

                       </Row>
                      <Row sm={100} md={100} lg={100} xl={100}>
                          <FormGroup mb="20px">
                              <StyledLabelWrap>
                                  <Label display="block" mb="5px" htmlFor="password">
                                      Password
                                  </Label>
                                  <Anchor path="/forgot-password" fontSize="13px">
                                      Forgot password?
                                  </Anchor>
                              </StyledLabelWrap>
                              <Input
                                  id="password"
                                  type="password"
                                  placeholder="Enter your password"
                                  feedbackText={errors?.password?.message}
                                  state={hasKey(errors, "password") ? "error" : "success"}
                                  showState={!!hasKey(errors, "password")}
                                  {...register("password", {
                                      required: "Password is required",
                                      minLength: {
                                          value: 6,
                                          message: "Minimum length is 6",
                                      },
                                  })}
                              />
                          </FormGroup>
                      </Row>
                      <Row sm={100} md={100} lg={100} xl={100}>
                          <Button type="submit" color="brand2" fullwidth>
                              Sign In
                          </Button>
                      </Row>
                       <StyledBottomText>
                           Don&apos;t have an account?{" "}
                           <Anchor path="/signup">Create an Account</Anchor>
                       </StyledBottomText>
                   </form>
               </Row>
           </Container>
        </StyledSignInWrap>
    );
};

export default SigninForm;
